import Vue from "vue";
import VueRouter from "vue-router";
import { auth } from "../helpers/adminFirebase";

Vue.use(VueRouter);

const routes = [
  { path: "/", redirect: "/orders" },
  {
    path: "/auth",
    name: "Auth",
    meta: { layout: "Login", requiresAuth: false, roles: [] },
    component: () => import("../views/Auth"),
  },
  {
    path: "/place",
    name: "Place",
    meta: { layout: "Main", requiresAuth: true, roles: ["owner"] },
    component: () => import("../views/Place"),
  },
  {
    path: "/workers",
    name: "Workers",
    meta: { layout: "Main", requiresAuth: true, roles: ["owner"] },
    component: () => import("../views/Workers"),
  },
  {
    path: "/menuitems",
    name: "Menu",
    meta: { layout: "Main", requiresAuth: true, roles: ["owner", "admin"] },
    component: () => import("../views/MenuItems"),
  },
  {
    path: "/orders",
    name: "Orders",
    meta: { layout: "Main", requiresAuth: true, roles: ["owner", "admin", "waiter"] },
    component: () => import("../views/Orders"),
  },
  {
    path: "/waiters",
    name: "Waiters",
    meta: { layout: "Main", requiresAuth: true, roles: ["owner", "admin", "waiter"] },
    component: () => import("../views/Waiters"),
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    meta: { layout: "Main", requiresAuth: true, roles: ["owner"] },
    component: () => import("../views/Dashboard"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "is-active",
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0, behavior: "smooth" };
  },
});

router.beforeEach(async (to, from, next) => {
  const requiresAuth = to.matched.some((x) => x.meta.requiresAuth);

  const roles = to.meta.roles;
  const idTokenResult = await auth.currentUser?.getIdTokenResult();
  const role = idTokenResult?.claims?.role;
  const accessGranted = roles.includes(role);
  console.log("beforeEach", roles, role, accessGranted);

  if (requiresAuth && !auth.currentUser) {
    next({ path: "Auth", replace: true });
  } else if (requiresAuth && !accessGranted) {
    next({ path: from.name, replace: true });
  } else {
    next();
  }
});

export default router;
