import Vue from "vue";
import Vuex from "vuex";

import _ from "lodash";

import moduleAuth from "./modules/moduleAuth";
import modulePlace from "./modules/modulePlace";
import moduleMenuItems from "./modules/moduleMenuItems";
import moduleMenuGroups from "./modules/moduleMenuGroups";
import moduleOrders from "./modules/moduleOrders";
import moduleWorkers from "./modules/moduleWorkers";
import moduleWaiters from "./modules/moduleWaiters";
import moduleDashboard from "./modules/moduleDashboard";
import moduleTest from "./modules/moduleTest";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loading: true,
    error: null,
    user: null,
    claims: [],
    place: null,
    workers: [],
    menuItems: [],
    menuGroups: [],
    orders: [],
    waiters: [],
    summary: [],
    uploadStatus: 0,
  },

  getters: {
    loading: (state) => {
      return state.loading;
    },
    menuItemsGroupped: (state) => {
      // return _.groupBy(state.menuItems, "group");
      // https://stackoverflow.com/questions/35590543/how-do-you-chain-functions-using-lodash
      return _.chain(state.menuItems)
        .orderBy(["rank"], ["asc"])
        .groupBy("group")
        .value();
    },
    // getOrders(state) {
    //   if (state.orders) {
    //     console.log("getOrders", state.orders);
    //     return (
    //       state.orders.map((order) => ({
    //         ...order,
    //         order: state.menuItems
    //           // .filter((item) => Object.keys(order.order).includes(item.id))
    //           .map((item) => ({ ...item, qty: order.order[item.id] }))
    //           .filter((item) => item.qty),
    //       })) || []
    //     );
    //   } else {
    //     return [];
    //   }
    // },
  },

  mutations: {
    setLoading(state, payload) {
      state.loading = payload;
    },
    setError(state, payload) {
      state.error = payload;
    },
    setUser(state, payload) {
      state.user = payload;
    },
    setClaims(state, payload) {
      state.claims = payload;
    },
    setPlace(state, payload) {
      state.place = payload;
    },
    setWorkers(state, payload) {
      state.workers = payload;
    },
    setMenuItems(state, payload) {
      state.menuItems = payload;
    },
    setMenuGroups(state, payload) {
      state.menuGroups = payload;
    },
    setOrders(state, payload) {
      state.orders = payload;
    },
    setUploadStatus(state, payload) {
      state.uploadStatus = payload;
    },
    setWaiters(state, payload) {
      state.waiters = payload;
    },
    setSummary(state, payload) {
      state.summary = payload;
    },
  },

  actions: {},

  modules: {
    moduleAuth,
    modulePlace,
    moduleWorkers,
    moduleMenuItems,
    moduleMenuGroups,
    moduleOrders,
    moduleWaiters,
    moduleDashboard,
    moduleTest,
  },
});
