import { db, storage } from "@/helpers/adminFirebase";

const debug = true;

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    savePlace(context, payload) {
      const { rootState, commit, dispatch } = context;
      const uid = rootState.user.uid;
      const place = payload.id;
      const role = rootState.claims?.role;

      debug && console.log(`savePlace UID ${uid} PLACE ${place} ROLE ${role}`);

      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .set(payload)
          .then(() => {
            debug && console.log("savePlace is done");
            dispatch("setClaims", { uid, place, role });
          })
          .catch((e) => commit("setError", e));
      }
    },

    editPlace(context, payload) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      debug && console.log(`editPlace UID ${uid} PLACE ${place} ROLE ${role}`);

      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .update({ ...payload, id: place })
          .then(() => {
            debug && console.log("editPlace is done");
          })
          .catch((e) => commit("setError", e));
      }
    },

    delPlace(context) {
      const { rootState, commit, dispatch } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      debug && console.log(`delPlace UID ${uid} PLACE ${place} ROLE ${role}`);

      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .delete()
          .then(() => {
            debug && console.log("delPlace is done");
            dispatch("setClaims", { uid, place: null, role });
          })
          .catch((e) => commit("setError", e));
      }
    },

    autoGetPlace(context) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      debug && console.log(`autoGetPlace UID ${uid} PLACE ${place} ROLE ${role}`);

      commit("setLoading", true);
      if (place) {
        db.collection("places")
          .doc(place)
          .onSnapshot((doc) => {
            console.log("autoGetPlace dome", doc.data());
            commit("setPlace", doc.data());
            commit("setLoading", false);
          });
      }
    },

    // https://www.genuitec.com/upload-files-using-vue-and-firebase/
    savePlaceImage(context, payload) {
      const { rootState, commit, dispatch } = context;
      const place = rootState.claims?.place;
      const { name, data } = payload;

      debug && console.log("savePlaceImage payload", payload);

      if (place) {
        const storageRef = storage.ref(`places/${place}/${name}`).put(data);
        storageRef.on(
          `state_changed`,

          (snapshot) => {
            commit("setUploadStatus", (snapshot.bytesTransferred / snapshot.totalBytes) * 100);
          },

          (e) => commit("setError", e),

          () => {
            commit("setUploadStatus", 100);
            storageRef.snapshot.ref.getDownloadURL().then((url) => {
              dispatch("editPlace", { [name]: url });
            });
          }
        );
      }
    },
  },
};
