import { functions } from "@/helpers/adminFirebase";

const debug = true;

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    async test(context) {
      const { rootState, commit } = context;
      const place = rootState.claims?.place;

      debug && console.log(`test PLACE ${place}`);

      if (place) {
        try {
          const responce = await functions.httpsCallable("createSummary")({ place });
          debug && console.log(`createWorker done`, responce);
        } catch (e) {
          commit("setError", e?.details);
        }
      }
    },
  },
};
