<template>
  <div>
    <!-- <button @click="test">xxxx</button> -->
    <component :is="layout">
      <router-view />
    </component>
  </div>
</template>

<script>
import Main from "@/layouts/Main";
import Login from "@/layouts/Login";

import { functions } from "./helpers/adminFirebase";
export default {
  methods: {
    test() {
      const testfunc = functions.httpsCallable("test");
      console.log("testfunc", typeof testfunc);

      testfunc({ text: "HUY2" })
        .then((res) => {
          console.log(res);
          alert(JSON.stringify({ HZ: "good", res }));
        })
        .catch((e) => {
          console.log(e);
          alert(JSON.stringify({ HZ: "error", e }));
        });
    },
  },
  components: { Main, Login },
  computed: {
    layout() {
      return this.$route.meta.layout;
    },
  },
};
</script>
