import Vue from "vue";
import Buefy from "buefy";
import "buefy/dist/buefy.css";

import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";

import Vuelidate from "vuelidate";

import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import { auth } from "./helpers/adminFirebase";
import { i18n } from "./plugins/i18n";

Vue.config.productionTip = false;

Vue.use(Buefy);
const options = {};
Vue.use(Toast, options);
Vue.use(Vuelidate);

let app;

auth.onAuthStateChanged(
  (user) => {
    if (!app) {
      app = new Vue({
        router,
        store,
        i18n,
        render: (h) => h(App),
      }).$mount("#app");
    }

    if (user) {
      store.commit("setUser", user);
      store.dispatch("getClaims").then(() => {
        store.dispatch("autoGetPlace");
        store.dispatch("autoGetWorkers");
        store.dispatch("autoGetMenuItems");
        store.dispatch("autoGetMenuGroups");
        store.dispatch("autoGetOrders");
        store.dispatch("autoGetWaiters");
        store.dispatch("autoGetSummary");
      });
    }
  },
  (error) => console.log(error)
);
