import { db, storage } from "@/helpers/adminFirebase";

const debug = true;

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    updateMenuItem(context, payload) {
      const { rootState, commit } = context;
      const place = rootState.claims?.place;

      if (place) {
        db.collection("places")
          .doc(place)
          .collection("menuItems")
          .doc(payload.id)
          .update(payload, { merge: true })
          .then(() => debug && console.log("updateMenuItem done"))
          .catch((e) => commit("setError", e));
      }
    },

    deleteMenuItem(context, payload) {
      const { rootState, commit } = context;
      const place = rootState.claims?.place;

      debug && console.log("deleteMenuItem payload", payload);

      if (place) {
        db.collection("places")
          .doc(place)
          .collection("menuItems")
          .doc(payload.id)
          .delete()
          .then(() => debug && console.log("deleteMenuItem is done"))
          .catch((e) => commit("setError", e));
      }
    },

    saveMenuItems(context, payload) {
      const { rootState } = context;
      const place = rootState.claims?.place;

      debug && console.log("saveMenuItems payload", payload);

      if (place) {
        const batch = db.batch();
        const menuCollection = db
          .collection("places")
          .doc(place)
          .collection("menuItems");

        payload.forEach((item) => {
          let ref = menuCollection.doc(item.id.toString());
          batch.set(ref, item, { merge: true });
        });
        return batch.commit();
      }
    },

    autoGetMenuItems(context) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;

      debug && console.log(`autoGetMenuItems UID ${uid} PLACE ${place}`);

      commit("setLoading", true);
      if (uid && place) {
        db.collection("places")
          .doc(place)
          .collection("menuItems")
          .onSnapshot((docs) => {
            let menuItems = [];
            docs.forEach((doc) => {
              menuItems.push({ id: doc.id, ...doc.data() });
            });
            debug && console.log(`autoGetMenuItems done`, menuItems);
            commit("setMenuItems", menuItems);
            commit("setLoading", false);
          });
      }
    },

    // https://www.genuitec.com/upload-files-using-vue-and-firebase/
    saveItemImage(context, payload) {
      const { rootState, commit, dispatch } = context;
      const place = rootState.claims?.place;
      const { id, data } = payload;

      debug && console.log("saveImage payload", payload);

      if (place) {
        const storageRef = storage.ref(`places/${place}/menuItems/${id}`).put(data);
        storageRef.on(
          `state_changed`,

          (snapshot) => {
            commit("setUploadStatus", (snapshot.bytesTransferred / snapshot.totalBytes) * 100);
          },

          (e) => commit("setError", e),

          () => {
            commit("setUploadStatus", 100);
            storageRef.snapshot.ref.getDownloadURL().then((url) => {
              dispatch("updateMenuItem", { id, image: url });
            });
          }
        );
      }
    },

    deleteItemImage(context, payload) {
      const { rootState, commit } = context;
      const place = rootState.claims?.place;
      const { id } = payload;

      debug && console.log("deleteItemImage payload", payload);
      if (place) {
        const storageRef = storage.ref(`places/${place}/menuItems/${id}`);
        console.log("storageRef", storageRef);
        storageRef
          .delete()
          .then(() => {
            debug && console.log("deleteItemImage is done");
          })
          .catch((e) => {
            commit("setError", e);
          });
      }
    },
  },
};
