import { db, functions } from "@/helpers/adminFirebase";

const debug = true;

/// https://www.reddit.com/r/reactjs/comments/fsw405/firebase_cloud_functions_cors_policy_error/

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    async updateUserDB(context, payload) {
      const { commit } = context;

      let data = {};
      const fields = ["place", "uid", "email", "name", "phone", "role", "status"];
      Object.keys(payload).forEach((key) => fields.includes(key) && (data[key] = payload[key]));

      debug && console.log(`VT STARTED updateUserDB with status "${payload.status}"`, data);
      try {
        await db
          .collection("places")
          .doc(payload.place)
          .collection("workers")
          .doc(payload.email)
          .set({ ...data }, { merge: true });
        debug && console.log(`VT DONE updateUserDB with status "${payload.status}"`);
      } catch (e) {
        debug && console.log(`VT ERROR updateUserDB with status "${payload.status}"`, e);
        commit("setError", e);
      }
    },

    async deleteUserDB(context, payload) {
      const { commit } = context;
      debug && console.log(`VT STARTED deleteUserDB`);
      try {
        await db
          .collection("places")
          .doc(payload.place)
          .collection("workers")
          .doc(payload.email)
          .delete();
        debug && console.log(`VT DONE deleteUserDB`);
      } catch (e) {
        debug && console.log(`VT ERROR deleteUserDB`, e);
        commit("setError", e);
      }
    },

    async createWorker(context, payload) {
      const { rootState, commit, dispatch } = context;
      const { password, email } = payload;
      const place = rootState.claims?.place;

      debug && console.log(`createWorker PLACE ${place} ROLE ${payload.role}`);

      if (place) {
        await dispatch("updateUserDB", { ...payload, place, status: "draft" });
        try {
          const user = await functions.httpsCallable("createWorker")({ email, password });
          debug && console.log(`createWorker done`, user);
          await functions.httpsCallable("changeUserClaims")({ uid: user.data.uid, place, role: payload.role });
          await dispatch("updateUserDB", { ...payload, place, status: "done", uid: user.data.uid });
        } catch (e) {
          await dispatch("deleteUserDB", { ...payload });
          commit("setError", e?.details);
        }
      }
    },

    async updateWorker(context, payload) {
      const { rootState, commit, dispatch } = context;
      const { uid } = payload;
      const place = rootState.claims?.place;

      debug && console.log(`editWorker PLACE ${place} ROLE ${payload.role}`, payload);
      if (place) {
        try {
          if (payload?.password) {
            await functions.httpsCallable("updateWorker")({ uid, password: payload.password });
          }
          if (payload?.role) {
            await functions.httpsCallable("changeUserClaims")({ uid, place, role: payload.role });
          }
          await dispatch("updateUserDB", { ...payload, place, status: "updated" });
        } catch (e) {
          commit("setError", e);
        }
      }
    },

    async deleteWorker(context, payload) {
      const { rootState, commit, dispatch } = context;
      const { uid } = payload;
      const place = rootState.claims?.place;

      debug && console.log(`deleteWorker PLACE ${place} UID ${uid}`, payload);
      if (place) {
        try {
          await functions.httpsCallable("deleteWorker")({ uid });
          await dispatch("deleteUserDB", { ...payload, place });
        } catch (e) {
          commit("setError", e?.details);
        }
      }
    },

    autoGetWorkers(context) {
      const { rootState, commit } = context;
      const place = rootState.claims?.place;

      debug && console.log(`autoGetWorkers PLACE ${place}`);

      commit("setLoading", true);
      if (place) {
        db.collection("places")
          .doc(place)
          .collection("workers")
          .onSnapshot((docs) => {
            let workers = [];
            docs.forEach((doc) => {
              workers.push({ email: doc.id, ...doc.data() });
            });
            debug && console.log("autoGetWorkers", workers);
            commit("setWorkers", workers);
            commit("setLoading", false);
          });
      }
    },
  },
};
