import { db } from "@/helpers/adminFirebase";

const debug = true;

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    updateMenuGroup(context, payload) {
      const { rootState, commit } = context;
      const place = rootState.claims?.place;

      if (place) {
        db.collection("places")
          .doc(place)
          .collection("menuGroups")
          .doc(payload.id)
          .set(payload, { merge: true })
          .then(() => debug && console.log("updateMenuGroup done"))
          .catch((e) => commit("setError", e));
      }
    },

    deleteMenuGroup(context, payload) {
      const { rootState, commit } = context;
      const place = rootState.claims?.place;

      debug && console.log("deleteMenuGroup payload", payload);

      if (place) {
        db.collection("places")
          .doc(place)
          .collection("menuGroups")
          .doc(payload.id)
          .delete()
          .then(() => debug && console.log("deleteMenuGroup is done"))
          .catch((e) => commit("setError", e));
      }
    },

    saveMenuGroups(context, payload) {
      const { rootState } = context;
      const place = rootState.claims?.place;

      debug && console.log("saveMenuGroups payload", payload);

      if (place) {
        const batch = db.batch();
        const menuCollection = db
          .collection("places")
          .doc(place)
          .collection("menuGroups");

        payload.forEach((item) => {
          let ref = menuCollection.doc(item.id.toString());
          batch.set(ref, item);
        });
        return batch.commit();
      }
    },

    autoGetMenuGroups(context) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;

      debug && console.log(`autoGetMenuGroups UID ${uid} PLACE ${place}`);

      commit("setLoading", true);
      if (uid && place) {
        db.collection("places")
          .doc(place)
          .collection("menuGroups")
          .onSnapshot((docs) => {
            let data = [];
            docs.forEach((doc) => {
              data.push({ id: doc.id, ...doc.data() });
            });
            debug && console.log(`autoGetMenuGroups done`, data);
            commit("setMenuGroups", data);
            commit("setLoading", false);
          });
      }
    },
  },
};
