import { db } from "@/helpers/adminFirebase";

const debug = true;

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    updateOrder(context, payload) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      debug && console.log("updateOrder payload", payload);
      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .collection("orders")
          .doc(payload.id)
          .update(payload)
          .then(() => debug && console.log("updateOrder done"))
          .catch((e) => commit("setError", e));
      }
    },

    autoGetOrders(context) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      let start = new Date();
      start.setHours(0, 0, 0, 0);

      let end = new Date();
      end.setHours(23, 59, 59, 999);

      debug && console.log("autoGetOrders start");

      commit("setLoading", true);
      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .collection("orders")
          .where("orderTime", ">", start)
          .where("orderTime", "<", end)
          .onSnapshot((docs) => {
            let orders = [];
            docs.forEach((doc) => {
              orders.push({ id: doc.id, ...doc.data() });
            });
            commit("setOrders", orders);
            commit("setLoading", false);
            debug && console.log("autoGetOrders done", orders);
          });
      }
    },
  },
};
