import { db } from "@/helpers/adminFirebase";

const debug = true;

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    getSummaryForDash(context) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      debug && console.log("getSummaryForDash start");

      commit("setLoading", true);
      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .collection("orders")
          .get()
          .where("capital", "==", true)
          .then((docs) => {
            let orders = [];
            docs.forEach((doc) => {
              orders.push({ id: doc.id, ...doc.data() });
            });
            commit("setOrdersDash", orders);
            commit("setLoading", false);
            debug && console.log("getSummaryForDash done", orders);
          });
      }
    },
    autoGetSummary(context) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      debug && console.log("autoGetSummary start");

      commit("setLoading", true);
      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .collection("summary")
          .onSnapshot((docs) => {
            let data = {};
            docs.forEach((doc) => {
              data[doc.id] = { id: doc.id, ...doc.data() };
            });
            // let data = [];
            // docs.forEach((doc) => {
            //   data.push({ id: doc.id, ...doc.data() });
            // });
            commit("setSummary", data);
            commit("setLoading", false);
            debug && console.log("autoGetSummary done", data);
          });
      }
    },
  },
};
