//https://oleg-agapov.medium.com/basic-single-page-application-using-vue-js-and-firebase-part-2-143a3084266f
import { auth, functions } from "@/helpers/adminFirebase";
import router from "@/router";

const debug = true;

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    login(context, payload) {
      const { commit } = context;
      const { email, password } = payload;

      commit("setLoading", true);
      auth
        .signInWithEmailAndPassword(email, password)
        .then((user) => {
          commit("setUser", user.user);
          router.push("/orders");
        })
        .catch((e) => {
          commit("setError", e);
        })
        .finally(() => commit("setLoading", false));
    },

    logout(context) {
      const { commit } = context;

      auth
        .signOut()
        .then(() => {
          commit("setUser", null);
          router.push("/auth?message=logout");
        })
        .catch((e) => commit("setError", e));
    },

    register(context, payload) {
      const { commit, dispatch } = context;
      const { email, password } = payload;

      commit("setLoading", true);
      auth
        .createUserWithEmailAndPassword(email, password)
        .then((res) => {
          dispatch("setClaims", { uid: res.user.uid, place: null, role: "owner" });
          commit("setUser", res.user);
          router.push("/orders");
        })
        .catch((e) => commit("setError", e))
        .finally(() => commit("setLoading", false));
    },

    setClaims(context, payload) {
      const { commit, dispatch } = context;
      const { uid, place, role } = payload;

      debug && console.log("setClaims payload ", payload);

      const changeUserClaims = functions.httpsCallable("changeUserClaims");
      changeUserClaims({ uid, place: place || null, role })
        .then(() => dispatch("getClaims"))
        .catch((e) => commit("setError", e));
    },

    getClaims(context) {
      const { commit } = context;

      debug && console.log("getClaims");

      return auth.currentUser
        .getIdTokenResult(true)
        .then((token) => commit("setClaims", token.claims))
        .catch((e) => commit("setError", e));
    },
  },
};
