import { db } from "@/helpers/adminFirebase";

const debug = true;

export default {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    updateWaiter(context, payload) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      debug && console.log("updateWaiter payload", payload);
      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .collection("waiters")
          .doc(payload.id)
          .update(payload)
          .then(() => debug && console.log("updateWaiter done"))
          .catch((e) => commit("setError", e));
      }
    },

    autoGetWaiters(context) {
      const { rootState, commit } = context;
      const uid = rootState.user.uid;
      const place = rootState.claims?.place;
      const role = rootState.claims?.role;

      debug && console.log("autoGetWaiters start");

      commit("setLoading", true);
      if (uid && place && role) {
        db.collection("places")
          .doc(place)
          .collection("waiters")
          .onSnapshot((docs) => {
            let waiters = [];
            docs.forEach((doc) => {
              waiters.push({ id: doc.id, ...doc.data() });
            });
            commit("setWaiters", waiters);
            commit("setLoading", false);
            debug && console.log("autoGetWaiters done", waiters);
          });
      }
    },
  },
};
